import {useState} from 'react'

export function Checkbox(props) {
  const [on, setOn] = useState(true);

  const Height = props.width * 0.5;
  const Width = props.width;

  const changeHandler = (evt) => {
    setOn(evt.target.checked ? false : true)
    props.handler()
  }

  return (
    <div className="checkbox">
      <input
        id={props.Id}
        type="checkbox"
        style={{ display: "none" }}
        onChange={changeHandler}
      />
      <label
        role="switch"
        htmlFor={props.Id}
        style={{
          width: Width + "px",
          display: "flex",
          height: Height,
          backgroundColor: on ? "lightgreen" : "gray",
          border: "1px solid #333333",
          borderRadius: Height / 2 + "px",
          boxSizing: "content-box",
          justifyContent: on ? "flex-end" : "flex-start",
          cursor: "pointer"
        }}
      >
        <div
          className="toggle"
          style={{
            width: Height + "px",
            height: Height + "px",
            backgroundColor: "#9dbef5",
            borderRadius: "50%",
            border: "solid #101e36 1px",
            boxSizing: "border-box"
          }}
        >
          <i className="moon-fill"></i>
        </div>
      </label>
    </div>
  );
}